* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

body {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #777777;
    font-size: 16px;
    letter-spacing: 2px;
    padding: 50px;
    background-color: white; }

.container {
    background-color: #F7F7F7; }

.header {
    position: relative;
    height: 95vh;
    background: url(../img/header-background.jpg) center/cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        top: 50px;
        left: 50px;
        height: 70px;
        width: 56px; }
    &__logo-1 {
        position: absolute;
        top: 112px;
        left: 70px;
        height: 15px;
        width: 16px; }
    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }

.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 50px;
        line-height: 60px;
        letter-spacing: 10px;
        text-transform: uppercase;
        margin-bottom: 10px;
        animation-name: LeftToRight;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }
    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: 5px;
        animation-name: RightToLeft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 65px; } }

.btn {
    &:link, &:visited {
        text-transform: uppercase;
        text-decoration: none;
        padding: 15px 40px;
        color: #777777;
        background: white;
        border-radius: 50px;
        display: inline-block;
        transition: all .5s; }
    &:hover {
        transform: translateY(-3px);
        box-shadow:(0px 4px 4px rgba(0, 0, 0, 0.3)) {} }
    &:active {
        transform: translateY(-2px);
        box-shadow:(0px 4px 4px rgba(0, 0, 0, 0.1)) {} } }



@keyframes LeftToRight {
    0% {
        opacity: 0;
        transform: translateX(-100px); }
    80% {
        transform: translateX(10px); }

    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes RightToLeft {
    0% {
        opacity: 0;
        transform: translateX(100px); }
    80% {
        transform: translateX(-10px); }

    100% {
        opacity: 1;
        transform: translateX(0px); } }
